import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);
const router = new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/view/installation',
      component: () => import(/* webpackChunkName: "installation" */ './views/installation.vue'),
    }, {
      path: '/view/quickStart',
      component: () => import(/* webpackChunkName: "quickStart" */ './views/quickStart.vue'),
    }, {
      path: '/view/i18n',
      component: () => import(/* webpackChunkName: "i18n" */ './views/i18n.vue'),
    }, {
      path: '/view/button',
      component: () => import(/* webpackChunkName: "button" */ './views/button.vue'),
    }, {
      path: '/view/input',
      component: () => import(/* webpackChunkName: "input" */ './views/input.vue'),
    }, {
      path: '/view/ipInput',
      component: () => import(/* webpackChunkName: "input" */ './views/ipInput.vue'),
    }, {
      path: '/view/subnetInput',
      component: () => import(/* webpackChunkName: "input" */ './views/subnetInput.vue'),
    }, {
      path: '/view/switch',
      component: () => import(/* webpackChunkName: "switch" */ './views/switch.vue'),
    }, {
      path: '/view/message',
      component: () => import(/* webpackChunkName: "switch" */ './views/message.vue'),
    }, {
      path: '/view/vtip',
      component: () => import(/* webpackChunkName: "switch" */ './views/vtip.vue'),
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    return { x: 0, y: 0 };
  },
});

router.beforeEach((to, from, next) => {
  if (to.path === '/') {
    next('/view/installation');
  }
  next();
});

export default router;
